/* Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  color: #333;
  line-height: 1.6;
}

/* Estilos do Header */
.header {
  background-color: #0044cc;
  color: white;
  padding: 1.5rem 2rem;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.header__title {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.header__nav {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.header__btn {
  background-color: white;
  color: #0044cc;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.header__btn:hover {
  background-color: #0033a0;
  color: white;
  transform: scale(1.05);
}

/* Estilos do Footer */
.footer {
  background-color: #e9ecef;
  color: #6c757d;
  text-align: center;
  padding: 1rem 2rem;
  font-size: 0.9rem;
  margin-top: 2rem;
}

/* Estilos gerais para o Main */
.app__main {
  width: 100%;
  margin: 2rem auto;
  padding: 1rem;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Estilos da página Inicial */
.inicio {
  text-align: center;
}

.inicio h2 {
  font-size: 1.8rem;
  color: #0044cc;
  margin-bottom: 1rem;
}

.inicio p {
  font-size: 1.2rem;
  color: #555;
}

/* Estilos do Formulário */
.formulario {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.formulario h2 {
  color: #0044cc;
  font-size: 1.6rem;
  margin-bottom: 1rem;
  text-align: center;
}

.formulario label {
  font-weight: bold;
  color: #555;
}

.formulario input,
.formulario select,
.formulario textarea {
  padding: 0.8rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  width: 100%;
}

.formulario textarea {
  resize: none;
  height: 100px;
}

.formulario__btn {
  background-color: #0044cc;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  align-self: center;
  transition: all 0.3s ease;
}

.formulario__btn:hover {
  background-color: #0033a0;
  transform: scale(1.05);
}

/* Estilos da Lista */
.lista {
  text-align: center;
}

.lista h2 {
  color: #0044cc;
  font-size: 1.6rem;
  margin-bottom: 1rem;
}

.lista__tabela {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.lista__tabela th,
.lista__tabela td {
  padding: 0.8rem;
  border: 1px solid #ddd;
  text-align: center;
}

.lista__tabela th {
  background-color: #0044cc;
  color: white;
  font-weight: bold;
}

.lista__tabela tr:nth-child(even) {
  background-color: #f2f2f2;
}

.lista__tabela tr:hover {
  background-color: #eaf3ff;
}

.lista button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.lista button:hover {
  background-color: #b02a37;
}

.form-mostrar {
  background-color: rgba(0, 0, 0, 0.1);
}

/* Estilos para Responsividade */
@media (max-width: 768px) {
  .header__nav {
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .formulario__btn {
    width: 100%;
  }

  .app__main {
    padding: 1rem;
  }

  .lista__tabela {
    font-size: 0.9rem;
  }
}
